import { NavLink } from 'react-router-dom';
import classes from './Navbar.module.css';

const setActive = ({ isActive }) => isActive ? classes.activeLink : '';

const Navbar = () => {
	return (
		<nav className={classes.nav}>
			<div className={classes.item}>
				<NavLink exact to='/profile' className={setActive}>Profile</NavLink>
			</div>
			<div className={classes.item}>
				<NavLink to='/dialogs' className={setActive}>Messages</NavLink></div>
			<div className={classes.item}>
				<NavLink to='/users' className={setActive}>Users</NavLink></div>
			<div className={classes.item}><NavLink to=''>News</NavLink></div>
			<div className={classes.item}><NavLink to=''>Music</NavLink></div>
			<div className={classes.item}><NavLink to=''>Settings</NavLink></div>
		</nav>
	)
}
export default Navbar;