import { connect } from "react-redux"
import { Redirect } from "react-router-dom"
import { Field, reduxForm } from "redux-form"
import { login } from "../../redux/auth-reducer"
import { required } from "../../utils/validators/validators"
import { createFieldForm, Input } from "../common/FormsControls/FormsControls"
import style from "../common/FormsControls/FormsControls.module.css"

const LoginForm = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      {createFieldForm('Email', 'email', [required], Input)}
      {createFieldForm('Password', 'password', [required], Input, { type: 'password' }, 'remember me')}
      {props.error && <div className={style.formSummaryError}>
        {props.error}
      </div>}
      {createFieldForm(null, 'rememberMe', [], Input, { type: 'checkbox' })}
      <div>
        <button>Login</button>
      </div>
    </form>
  )
}

const LoginReduxForm = reduxForm({ form: 'login' })(LoginForm)

const Login = (props) => {

  const onSubmit = (formData) => {
    props.login(formData.email, formData.password, formData.rememberMe)
  }

  if (props.isAuth) {
    return <Redirect to={'/profile'} />
  }

  return (
    <div>
      <h1>
        Login
      </h1>
      <LoginReduxForm onSubmit={onSubmit} />
    </div>
  )
}

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, { login })(Login);